.arrow-up {
  position: fixed;
  right: -60px;
  bottom: 95px;
  padding: 0;
  width: 50px;
  height: 50px;
  border: 0;
  outline: 0;
  cursor: pointer;
  z-index: 99;
  transition: all 0.3s;
}

.arrow-up--visible {
  right: 30px;
}

